import Header from './Header'
import Layout from '../components/Layout'
import About from '../components/About'
import Skills from '../components/Skills'
import Experience from '../components/Experience'
import Footer from '../components/Footer'
import Nav from '../components/Nav'
import Head from 'next/head'
import TrackVisibility from 'react-on-screen'

class App extends React.Component {

	constructor(props) {
		super(props)

		this.state = {
			currentSection: null
		}

		this.changeSection = this.changeSection.bind(this)
	}

	componentDidMount() {
		this.scrollToComponent = require('react-scroll-to-component').bind(this)
		window.addEventListener('scroll', this.handleScroll)
	}

	componentWillUnmount() {
		window.removeEventListener('scroll')
	}

	handleScroll() {
		console.log('handling the scroll')
	}

	changeSection(href) {
		event.preventDefault()
		this.setState({
			currentSection: this[href.substr(1) + 'Ref'], 
			navActiveItem: href
		}, () => {
			this.scrollToComponent(this.state.currentSection, { offset: 0, align: 'middle', duration: 500, ease:'inCirc' });
		})
	}

	render () {
		return (
			<Layout>
				<Head>
					<title>{this.props.meta.title}</title>
					<meta name="viewport" content="width=device-width, initial-scale=1"/>
					<link href="https://fonts.googleapis.com/css?family=Raleway:300,400,600,700" rel="stylesheet" />
				</Head>
				<Header
					{...this.props.sectionHeader}
					ref={(section) => { this.headerRef = section }}
				> 
					<TrackVisibility>
						<Nav
							{...this.props.navigation}
							{...this.state}
							changeSection={this.changeSection}
						/>
					</TrackVisibility>
				</Header>
				<About 
					{...this.props.sectionAbout}
					ref={(section) => { this.aboutRef = section }}
				/>
				<Skills 
					{...this.props.sectionSkills}
					ref={(section) => { this.skillsRef = section }}
				/>
				<Experience
					{...this.props.sectionExperience}
					ref={(section) => { this.experienceRef = section }}
				/>
				<Footer/>
			</Layout>
		)
	}
}

export default App
